<template>
  <main class="journal">
    <div class="cm mcm">
    <section class="journal_list">
      <div class="page_title">
        <h1>Journal</h1>
      </div>
      <div class="yearl">
        <template v-for="(item, i) in sortedArticles">
          <p :key="i" v-if="item.year" class="year">
            <label>{{item.year}}</label>
          </p>
          <div :key="i" v-else class="journali">
            <div class="num">
              <label class="minapper">{{0 + item._idx}}</label>
            </div>
            <div class="title">
              <router-link :to="{ name: 'article', params: { id: item.id }}">{{item.title}}</router-link>
            </div>
          </div>
        </template>
      </div>
    </section>
    </div>
  </main>
</template>

<script>
  import { DateTime } from 'luxon'
  import { mapActions, mapGetters } from 'vuex'

  export default {
    name: 'journal',
    computed: {
      articlesCount () {
        let count = 0
        for (let year in this.articles) {
          if (this.articles.hasOwnProperty(year)) {
            count += this.articles[year].length
          }
        }
        return count
      },
      sortedArticles () {
        let arr = []
        let index = this.articlesCount

        function sortProjects (a, b) {
          return (+DateTime.fromISO(a.releaseYear)) - (+DateTime.fromISO(b.releaseYear))
        }

        function pad (n, width, z) {
          z = z || '0'
          n = n + ''
          return n.length >= width ? n : new Array(width - n.length + 1).join(z) + n
        }

        Object.keys(this.articles).sort().reverse().forEach(year => {
          arr.push({ year })

          this.articles[year].slice().sort(sortProjects).forEach(article => {
            arr.push({
              ...article,
              _idx: pad(--index, Math.floor(Math.log10(this.articlesCount)) + 1)
            })
          })
        })
        return arr
      },

      ...mapGetters({
        articles: 'articles'
      })
    },

    methods: {
      ...mapActions({
        load: 'articles'
      })
    },

    beforeRouteEnter (to, from, next) {
      next(vm => {
        vm.load()
      })
    }
  }
</script>

